<template>
    <nav>
      <ul>
        <li><router-link to="/">Home</router-link></li>
        <li><router-link to="/about">About</router-link></li>
        <li @mouseover="showDropdown = true" @mouseleave="showDropdown = false">
          Dropdown
          <ul v-if="showDropdown" class="dropdown">
            <li><router-link to="/link1">Link 1</router-link></li>
            <li><router-link to="/link2">Link 2</router-link></li>
            <!-- Add more links as needed -->
          </ul>
        </li>
      </ul>
    </nav>
  </template>
  
  <script>
  export default {
    name: 'NavbarComponent',
    data() {
      return {
        showDropdown: false,
      }
    },
  }
  </script>
  
  <style scoped>
  nav {
    background-color: #333;
    color: white;
    padding: 10px;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  li {
    float: left;
  }
  
  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  li a:hover {
    background-color: #111;
  }
  
  .dropdown {
    display: block;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 1;
  }
  
  .dropdown a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  </style>./Navbar.vue